<style scoped>
.layout {
    position: relative;
    border-radius: 4px;
    height: 100%;
}

.layout-logo {
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 0px;
    left: 10px;
}

.layout-header {
    height: 60px;
    background: #fff;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .1);
}

.layout-copy {
    text-align: center;
    padding: 10px 0 20px;
    color: #9ea7b4;
}

.layout-ceiling {
    background: #464c5b;
    padding: 10px 0;
    overflow: hidden;
}

.layout-ceiling-main {
    float: right;
    margin-right: 15px;
    margin-top: 5px;
}

.layout-ceiling-main a {
    color: #9ba7b5;
}
.login-form{
	width: 600px;
	padding: 100px;
}
</style>
<template>
    <div class="layout">
        <div class="layout-ceiling">
            <div class="layout-logo"></div>
            <div class="layout-ceiling-main">
                <a href="#"></a>
            </div>
        </div>
        <div class="login-form">
            <Form ref="loginForm" :model="loginForm" :rules="ruleValidate" :label-width="80">
                <Form-item label="" prop="username">
                    <Input v-model="loginForm.username" placeholder=""></Input>
                </Form-item>
                <Form-item label="" prop="password">
                    <Input type="password" v-model="loginForm.password" placeholder=""></Input>
                </Form-item>
                <Form-item>
                    <Button type="primary" @click="handleSubmit('loginForm')"></Button>
                </Form-item>
            </Form>
        </div>
    </div>
</template>
<script>
import api from "../../api/api";
import md5 from "md5";

export default {
    data() {
            return {
                loginForm: {
                    username: '',
                    password: ''
                },
                ruleValidate: {
                    username: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    password: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }]
                }
            }
        },
        methods: {
            handleSubmit(name) {
                this.$refs[name].validate(async (valid) => {
                    if (valid) {
                    	try{
	                    	let result = await api.ILogin({
	                    		url:"/user/login",
	                    		data:{
                                    username:this.loginForm.username,
                                    password:md5(this.loginForm.password)
                                }
	                    	})
                            if(result.code !== 0){
                                this.$Message.error(result.msg)
                            }else{
                                console.log(result);
                            }
	                    }catch(error){
	                    	console.log(error);
	                    	this.$Message.error(error);
                    	}
                    } else {
                        this.$Message.error('!');
                    }
                })
            }
        }
}
</script>



